//  action creator
import {
    USER_DATA,
    REMOVE_USER_DATA
} from './action-types';
import { UserBasicConfig } from 'components/home/Header';

// TODO: 
// register
export const transform_user = (user: UserBasicConfig)=>({
    type: USER_DATA,
    data: user
}) 

// redux
export const remove_user = (user: UserBasicConfig)=>({
    type: REMOVE_USER_DATA,
    data: user
}) 

